#toptitle h1 {
        text-align: center;
        display: block;
        font-size: 200%;
        font-weight: 600;
        margin-bottom: .25in;
}
#subtitle {
        text-align: center;
        display: block;
        font-size: 150%;
        margin-bottom: .5in;
}
h1 {
	font-size: 175%;
	font-weight: 900;
}
h2 {
	font-size: 125%;
	font-weight: 900;
}
body {
        max-width: 40em;
        text-align: justify;
}

div.quote div.blockcontent {
	margin: 2em;
	border: 0em;
	padding: 0em;
}

div.warning {
	margin-top: 1.2em;
	margin-bottom: 1.3em;
	padding: 0em;
}

div.warning > div.blockcontent {
	background-color: #ffe4e4;
	border-width: 1px;
	border-style: solid;
	border-color: #f66;
}

div.breakword {
	margin-top: 1.2em;
	margin-bottom: 1.3em;
}

div.breakword > div.blockcontent {
	background: #f6f6f6;
	word-wrap: break-word;
}

#layout-content p {
        text-indent: 1em;
        margin: 0em;
}
#footer p {
	text-indent: 0em;
}

#layout-content blockquote p {
	text-indent: 0em;
}

#layout-content li p{
	text-indent: 0em;
}
